.loader__container {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 1001;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    width: 100vh;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: radial-gradient(
        42.74% 42.74% at 50.08% 49.92%,
        rgba(218, 218, 218, 0.84) 0%,
        rgba(218, 218, 218, 0.51) 43.75%,
        rgba(218, 218, 218, 0.17) 68.75%,
        rgba(218, 218, 218, 0) 100%
    );
}

.lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.lds-ripple div {
    position: absolute;
    border: 4px solid#F0002F;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
}

@keyframes lds-ripple {
    0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1;
    }

    100% {
        top: 0px;
        left: 0px;
        width: 72px;
        height: 72px;
        opacity: 0;
    }
}
